.modal {
  position: fixed;
  z-index: 10;
  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,.5);
}

.modal--active{
  display: flex;
}

.modal__container {
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 490px;
  padding: 33px 39px;
  box-sizing: border-box;
  background-color: $blue-main;
}

.modal__title {
  margin-bottom: 7px;
  font-family: $roboto-condensed;
  font-size: 28px;
  line-height: 1.4;
  color: $white;
}

.modal__text {
  color: $white;
  line-height: 22px;
  margin-bottom: 31px;
}

.modal__form {
  input[type=text],
  input[type=tel],
  button,
  textarea {
    width: 100%;
    margin-bottom: 30px;
  }

  label:last-child {
    width: 337px;
    padding-left: 40px;
  }

  input[type=checkbox] + span {
    &::before {
      left: 0px;
    }
  }

  input[type=checkbox]:checked + span {
    &::after {
      left: 5px;
    }
  }
}

.modal-button {
  position: absolute;
  border: none;
  background: none;
  width: 30px;
  height: 30px;
  right: 25px;
  top: 5px;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    width: 23px;
    height: 1px;
    background-color: $white;
    right: 5px;
    top: 15px;
    transform: rotate(135deg);
  }

  &::after {
    content: "";
    position: absolute;
    width: 23px;
    height: 1px;
    background-color: $white;
    right: 5px;
    top: 15px;
    transform: rotate(45deg);
  }
}
